@charset "UTF-8";
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 100%;
  font-family: sans-serif;
  scroll-behavior: smooth;
}

body {
  display: flex;
  justify-content: center;
  background: #000;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  max-width: 1920px;
}
@media (max-width: 320px) {
  .wrapper.active {
    max-height: 900px;
  }
}

.swiper-container {
  width: 100%;
  height: 100%;
}
@media (max-width: 768px) {
  .swiper-container {
    display: none;
  }
}

.swiper-wrapper {
  width: 100%;
  height: 100%;
}
@media (max-width: 992.99px) {
  .swiper-wrapper {
    max-height: 1050px;
  }
}
@media (max-width: 992.99px) and (max-width: 768px) {
  .swiper-wrapper {
    max-height: inherit;
  }
}

.swiper-slide {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.screen__img {
  width: 100%;
  height: 100%;
  max-height: 1080px;
  min-height: 1080px;
  object-fit: cover;
}
@media (max-width: 768px) {
  .screen__img {
    min-height: 576px;
  }
}

@media (max-width: 992.99px) {
  .screen__img-1 {
    display: none;
  }
}

.screen__img-768 {
  display: none;
}
@media (max-width: 992.99px) {
  .screen__img-768 {
    display: flex;
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 600px) {
  .screen__img-768 {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 400px) {
  .screen__img-768 {
    display: none;
  }
}

.screen__img-320 {
  display: none;
}
@media (max-width: 400px) {
  .screen__img-320 {
    display: flex;
    width: 100%;
    height: 100%;
    min-height: 480px;
  }
}

.screen-2 {
  object-position: center 41%;
  width: 106%;
  max-width: 107%;
  height: 101%;
}

.swiper-next-gallery,
.swiper-prev-gallery {
  cursor: pointer;
  z-index: 9999 !important; /* на всякий случай */
}